import React from 'react';
import './Square.css';
import Event from 'events';



class Square extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            value: props.item,
        };
    }


    myfunction = (event) => {
        //console.log("Its Working");
        if(event.ctrlKey){
            this.props.CBchangeItem(this.props.item.key);
        }
        
    }

    render(){
        this.state.value = this.props.item;
        //console.log("square rerendered");
        return(
                <div onMouseDown={this.myfunction} onMouseEnter={this.myfunction} className="sortItem">
                        <p className="sortItemText">{this.props.item.value}</p>                        
                </div>        
        );
    }
}

export default Square;